.movies a{width:48%;display:flex;align-items:flex-start;justify-content:space-between;text-decoration:none}.movies .movie{width:100%;background-color:white;margin-bottom:70px;display:flex;align-items:flex-start;justify-content:space-between;font-weight:300;padding:20px;border-radius:5px;color:#adaeb9;box-shadow:0 13px 27px -5px rgba(50,50,93,0.25),0 8px 16px -8px rgba(0,0,0,0.3),0 -6px 16px -6px rgba(0,0,0,0.025)}.movie img{position:relative;top:-50px;max-width:150px;width:100%;margin-right:30px;box-shadow:0 30px 60px -12px rgba(50,50,93,0.25),0 18px 36px -18px rgba(0,0,0,0.3),0 -12px 36px -8px rgba(0,0,0,0.025)}.movie .movie__title,.movie .movie__year{margin:0;font-weight:300}.movie .movie__title{margin-bottom:5px;font-size:20px;color:#2c2c2c;height:27px;white-space:nowrap;text-overflow:ellipsis;overflow:hidden;width:24vw}.movie .movie__genres{list-style:none;padding:0;margin:0;display:flex;margin:5px 0px;white-space:nowrap}.movie .movie__summary{font-size:14px}.movie__genres li,.movie .movie__year{margin-right:10px;font-size:12px}

*{box-sizing:border-box}body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;background-color:#eff3f7;height:100%}html,body,#potato,.container{height:100%;display:flex;justify-content:center}.container{padding-top:50px}.loader{width:100%;height:100%;display:flex;justify-content:center;align-items:center;font-weight:300}.movies{display:flex;justify-content:space-between;align-items:flex-start;flex-wrap:wrap;padding:50px;padding-top:70px;width:96%}

.about__container{box-shadow:0 13px 27px -5px rgba(50,50,93,0.25),0 8px 16px -8px rgba(0,0,0,0.3),0 -6px 16px -6px rgba(0,0,0,0.025);padding:20px;border-radius:5px;background-color:white;margin:0 auto;margin-top:100px;width:60%;font-weight:300}.about__container span:first-child{font-size:20px}.about__container span:last-child{display:block;margin-top:10px}

.detail{width:70%;background-color:white;margin-bottom:70px;display:flex;align-items:flex-start;justify-content:space-between;font-weight:300;padding:20px;border-radius:5px;color:#adaeb9;margin:100px auto;box-shadow:0 13px 27px -5px rgba(50,50,93,0.25),0 8px 16px -8px rgba(0,0,0,0.3),0 -6px 16px -6px rgba(0,0,0,0.025)}.detail img{position:relative;top:-50px;max-width:150px;width:100%;margin-right:30px;box-shadow:0 30px 60px -12px rgba(50,50,93,0.25),0 18px 36px -18px rgba(0,0,0,0.3),0 -12px 36px -8px rgba(0,0,0,0.025)}.detail .detail__title,.detail .detail__year{margin:0;font-weight:300}.detail .detail__title{margin-bottom:5px;font-size:24px;color:#2c2c2c}.detail .detail__genres{list-style:none;padding:0;margin:0;display:flex;margin:5px 0px;white-space:nowrap}.detail .detail__summary{font-size:14px}.detail__genres li,.detail .detail__year{margin-right:10px;font-size:14px}

.nav{position:fixed;z-index:1;top:10px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);display:flex;flex-direction:row;background-color:white;padding:10px 20px;box-shadow:0 13px 27px -5px rgba(50,50,93,0.25),0 8px 16px -8px rgba(0,0,0,0.3),0 -6px 16px -6px rgba(0,0,0,0.025);border-radius:5px}.nav a{text-decoration:none;color:#222;text-transform:uppercase;font-size:14px;text-align:center;font-weight:600}.nav a:not(:last-child){margin-right:40px}

